import Snap from 'snapsvg';
require('./scss/main.scss');

class Logo {
  constructor() {
    this.setOptions();
    this.init();
    this.lastTheme = 0;
  }

  setOptions() {
    this.opts = {
      versions: {
        0: 3,
        1: 4,
      },
      letters: {
        0: 9,
        1: 5,
      },
      delay: 2500,
      dur: 500,

      easing: function(n, s) {
        if (s == undefined) s = 1.70158;
        if ((n *= 2) < 1)
          return 0.5 * (Math.pow(n, 2) * (((s *= 1.1) + 1) * n - s));
        return 0.5 * (Math.pow((n -= 2), 2) * (((s *= 1.1) + 1) * n + s) + 2);
      },
    };
  }

  init() {
    this.masters = [];
    let masters = document.querySelectorAll('#logo path[data-master]');
    [...masters].forEach((el, i) => {
      let snapEl = Snap(el);
      this.masters.push(snapEl);
    });
    this.animate(0);
  }

  animate(masterIndex) {
    let snapEl = this.masters[masterIndex];
    let letter = parseInt(snapEl.attr('data-letter'));
    let word = parseInt(snapEl.attr('data-word'));
    let version = parseInt(snapEl.attr('data-version'));
    let callback = () => {};
    let nextVersion = version + 1 == this.opts.versions[word] ? 0 : version + 1;
    let nextPath = document.querySelector(
      `path[data-word="${word}"][data-letter="${letter}"][data-version="${nextVersion}"]`
    );
    let nextMasterIndex =
      masterIndex + 1 === this.masters.length ? 0 : masterIndex + 1;
    let delay = nextMasterIndex === 0 ? this.opts.delay : 100;

    snapEl
      .animate(
        {
          d: Snap(nextPath).attr('d'),
        },
        this.opts.dur,
        this.opts.easing,
        callback
      )
      .attr({ 'data-version': nextVersion });

    if (masterIndex === 0) {
      let themeCount = 6;
      let rand = Math.ceil(Math.random() * themeCount);
      if (this.lastTheme === rand) {
        if (rand + 1 > themeCount) {
          rand--;
        } else {
          rand++;
        }
      }
      document.getElementById('app').className = `theme${rand}`;
      this.lastTheme = rand;
    }

    setTimeout(() => {
      this.animate(nextMasterIndex);
    }, delay);
  }
}

const logo = new Logo();

const easeInOutBack = function(n, s) {
  if (s == undefined) s = 1.70158;
  if ((n *= 2) < 1) return 0.5 * (Math.pow(n, 2) * (((s *= 1.1) + 1) * n - s));
  return 0.5 * (Math.pow((n -= 2), 2) * (((s *= 1.1) + 1) * n + s) + 2);
};
